export function getInterpolatedColor(value, colorLight, colorDark) {
    var color;
    value = value > 1 ? 1 : (value < 0 ? 0 : value);
    if (value === 0) {
        color = colorDark;
    }
    else if (value === 1) {
        color = colorLight;
    }
    else {
        color = [
            Math.max(0, Math.min(255, colorDark[0] + (colorLight[0] - colorDark[0]) * value)),
            Math.max(0, Math.min(255, colorDark[1] + (colorLight[1] - colorDark[1]) * value)),
            Math.max(0, Math.min(255, colorDark[2] + (colorLight[2] - colorDark[2]) * value)),
            Math.max(0, Math.min(255, colorDark[3] + (colorLight[3] - colorDark[3]) * value)),
        ];
    }
    return color;
}
export function convertColorDecToHex(color) {
    return '#' + (((color[0] & 0xff) << 16) | ((color[1] & 0xff) << 8) | (color[2] & 0xff))
        .toString(16)
        .padStart(6, '0');
}
export function convertHexToRgb(hex) {
    var color = parseInt(hex.substring(1), 16);
    return [(color >> 16) & 0xFF, (color >> 8) & 0xFF, color & 0xFF, 0xFF];
}
export function mixColors(frontRGBA, bgRGBA) {
    if (bgRGBA[3] < 20) {
        return frontRGBA.slice();
    }
    else if (frontRGBA[3] < 20) {
        return bgRGBA.slice();
    }
    var bgR = bgRGBA[0] / 255;
    var bgG = bgRGBA[1] / 255;
    var bgB = bgRGBA[2] / 255;
    var bgA = bgRGBA[3] / 255;
    var fgR = frontRGBA[0] / 255;
    var fgG = frontRGBA[1] / 255;
    var fgB = frontRGBA[2] / 255;
    var fgA = frontRGBA[3] / 255;
    var a = (1 - fgA) * bgA;
    var alpha = a + fgA;
    var r = (a * bgR + fgA * fgR) / alpha;
    var g = (a * bgG + fgA * fgG) / alpha;
    var b = (a * bgB + fgA * fgB) / alpha;
    return [
        Math.max(0, Math.min(255, (r * 255) | 0)),
        Math.max(0, Math.min(255, (g * 255) | 0)),
        Math.max(0, Math.min(255, (b * 255) | 0)),
        Math.max(0, Math.min(255, (alpha * 255) | 0))
    ];
}
