import { Editor } from "game/editor/editor";
import { Viewer } from "game/viewer/viewer";
document.addEventListener('DOMContentLoaded', function () {
    var cont = document.querySelector('.snow-editor-container');
    if (cont) {
        var editor = new Editor(parseInt(cont.dataset.userId, 10), cont.dataset.userName);
        cont.appendChild(editor.el);
    }
    var viewCont = document.querySelector('.snow-view-container');
    if (viewCont) {
        var viewer = new Viewer();
        viewer.load(document.querySelector('script#snow-data').textContent).then();
        viewCont.appendChild(viewer.el);
    }
});
